import { DataProvider, GetListResult, GetOneParams, GetOneResult, UpdateParams, UpdateResult, GetListParams, Identifier, ValidUntil } from "react-admin"
import { EVENTS_URL } from "../api-urls"
import { get, patch, put, getWithPagination } from "../common/fetch.utils"
import { ALL_RESULTS } from "../common/filter.values"
import { EventDto } from "../dto/event.dto"
import { Event } from "./event"
import { DateTime } from "../common/date-time"
import { EventStatus } from "../enum/EventStatus"
import { mapSortEventParam } from './event-sort.mapper'
import { EventDevices } from "../dto/event-devices.dto"
import { EventCompanies } from "../dto/event-companies.dto"
import { EventObjects } from "../dto/event-objects.dto"

type EventUpdateRequest = {
  id: number,
  name?: string,
  dateStart?: DateTime,
  dateEnd?: DateTime,
  status?: EventStatus
}

interface EventListParams extends GetListParams {
  readonly sort: {
    readonly field: keyof Event
    readonly order: 'ASC' | 'DESC'
  }
  readonly filter:
  {
    readonly name: string
  }
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { name },
      sort: { field, order },
      pagination: { page, perPage }
    }: EventListParams,
  ): Promise<GetListResult<Event>> => {
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortEventParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)
    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const { data: events, range: { total } } = await getWithPagination<EventDto[]>(`${EVENTS_URL}/GetByConditions/`, `${name ? `e=>e.Name.Contains("${name}")` : ALL_RESULTS}?${pathParams}`)

    const data = events.map((event) => ({
      id: event.id,
      name: event.name,
      dateStart: event.dateStart,
      dateEnd: event.dateEnd,
      status: event.status
    }))

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<Event>> => {
    const event = await get<EventDto>(EVENTS_URL, `/${id}`)

    return {
      data: event
    }
  },
  update: async (
    resource: string,
    { id, data, previousData }: UpdateParams
  ): Promise<UpdateResult> => {
    const { name, dateStart, dateEnd } = data

    let eventUpdate = data
    if (previousData) {
      const { name: previousName, dateStart: previousDateStart, dateEnd: previousDateEnd } = previousData
      eventUpdate = await patch<EventUpdateRequest, EventDto>(EVENTS_URL, {
        id: parseInt(id as string, 10),
        name: name ?? previousName,
        dateStart: dateStart ?
          new Date(new Date(dateStart).getTime() - (new Date(dateStart).getTimezoneOffset() * 60000)).toJSON() as DateTime :
          previousDateStart,
        dateEnd: dateEnd ?
          new Date(new Date(dateEnd).getTime() - (new Date(dateEnd).getTimezoneOffset() * 60000)).toJSON() as DateTime :
          previousDateEnd
      })
    }

    return Promise.resolve({
      data: { id, ...data, ...eventUpdate }
    })
  },
  activate: async (
    resource: string,
    { eventId }: ChangeStateParams
  ): Promise<ChangeStateResult<ChangeStateResponse>> => {
    const activate = await put<undefined, ChangeStateResponse>
      (`${EVENTS_URL}/ActivateAsyncJob/${eventId}`, undefined)
    return {
      data: activate
    }
  },
  deactivate: async (
    resource: string,
    { eventId }: ChangeStateParams
  ): Promise<ChangeStateResult<ChangeStateResponse>> => {
    const deactivate = await put<undefined, ChangeStateResponse>
      (`${EVENTS_URL}/DeactivateAsyncJob/${eventId}`, undefined)
    return {
      data: deactivate
    }
  },
  getCompaniesList: async (
    resource: string,
    { eventId }: GetCompaniesParams
  ): Promise<EventCompaniesResult<EventCompanies>> => {
    const companies = await put<undefined, EventCompanies>
      (`${EVENTS_URL}/GetCompanies/${eventId}`, undefined)
    return {
      data: companies
    }
  },
  attachCompanies: async (
    resource: string,
    params: EventCompanies
  ): Promise<EventCompaniesResult<EventCompanies>> => {
    const eventCompanies = await put<EventCompanies, EventCompanies>
      (`${EVENTS_URL}/AttachCompanies`, params)
    return {
      data: eventCompanies
    }
  },
  detachCompanies: async (
    resource: string,
    params: EventCompanies
  ): Promise<EventCompaniesResult<EventCompanies>> => {
    const eventCompanies = await put<EventCompanies, EventCompanies>
      (`${EVENTS_URL}/DetachCompanies`, params)
    return {
      data: eventCompanies
    }
  },
  attachObjects: async (
    resource: string,
    params: EventObjects
  ): Promise<EventObjectsResult<EventObjects>> => {
    const eventObjects = await put<EventObjects, EventObjects>
      (`${EVENTS_URL}/AttachObjects`, params)
    return {
      data: eventObjects
    }
  },
  detachObjects: async (
    resource: string,
    params: EventObjects
  ): Promise<EventObjectsResult<EventObjects>> => {
    const eventObjects = await put<EventObjects, EventObjects>
      (`${EVENTS_URL}/DetachObjects`, params)
    return {
      data: eventObjects
    }
  },
  getDevicesList: async (
    resource: string,
    { eventId }: GetDevicesParams
  ): Promise<EventDevicesResult<EventDevices>> => {
    const devices = await put<undefined, EventDevices>
      (`${EVENTS_URL}/GetDevices/${eventId}`, undefined)
    return {
      data: devices
    }
  },
  attachDevices: async (
    resource: string,
    params: EventDevices
  ): Promise<EventDevicesResult<EventDevices>> => {
    const eventDevices = await put<EventDevices, EventDevices>
      (`${EVENTS_URL}/AttachDevices`, params)
    return {
      data: eventDevices
    }
  },
  detachDevices: async (
    resource: string,
    params: EventDevices
  ): Promise<EventDevicesResult<EventDevices>> => {
    const eventDevices = await put<EventDevices, EventDevices>
      (`${EVENTS_URL}/DetachDevices`, params)
    return {
      data: eventDevices
    }
  }
} as EventDataProvider

interface EventDataProvider extends DataProvider {
  activate: (
    resource: string,
    params: ChangeStateParams
  ) => Promise<ChangeStateResult<ChangeStateResponse>>;

  deactivate: (
    resource: string,
    params: ChangeStateParams
  ) => Promise<ChangeStateResult<ChangeStateResponse>>;

  getCompaniesList: (
    resource: string,
    params: GetCompaniesParams
  ) => Promise<EventCompaniesResult<EventCompanies>>;

  attachCompanies: (
    resource: string,
    params: EventCompanies
  ) => Promise<EventCompaniesResult<EventCompanies>>;

  detachCompanies: (
    resource: string,
    params: EventCompanies
  ) => Promise<EventCompaniesResult<EventCompanies>>;

  getObjectsList: (
    resource: string,
    params: GetObjectsParams
  ) => Promise<EventObjectsResult<EventObjects>>;

  attachObjects: (
    resource: string,
    params: EventObjects
  ) => Promise<EventObjectsResult<EventObjects>>;

  detachObjects: (
    resource: string,
    params: EventObjects
  ) => Promise<EventObjectsResult<EventObjects>>;

  getDevicesList: (
    resource: string,
    params: GetDevicesParams
  ) => Promise<EventDevicesResult<EventDevices>>;

  attachDevices: (
    resource: string,
    params: EventDevices
  ) => Promise<EventDevicesResult<EventDevices>>;

  detachDevices: (
    resource: string,
    params: EventDevices
  ) => Promise<EventDevicesResult<EventDevices>>;
};

export interface ChangeStateParams {
  eventId: Identifier;
}

export interface ChangeStateResponse {
  jobId: Identifier;
}

export interface ChangeStateResult<RecordType = ChangeStateResponse> {
  data: RecordType;
}

export interface GetCompaniesParams {
  eventId: Identifier;
}

export interface GetObjectsParams {
  eventId: Identifier;
}

export interface GetDevicesParams {
  eventId: Identifier;
}

export interface EventCompaniesResult<RecordType = EventCompanies> {
  data: RecordType;
  validUntil?: ValidUntil;
}

export interface EventObjectsResult<RecordType = EventObjects> {
  data: RecordType;
  validUntil?: ValidUntil;
}

export interface EventDevicesResult<RecordType = EventDevices> {
  data: RecordType;
  validUntil?: ValidUntil;
}

export default provider