import {
  Card,
  CardContent,
  CardHeader,
  createStyles,
  Grid,
  makeStyles,
  Theme,
} from '@material-ui/core'
import React from 'react'
import {
  ResourceContextProvider,
  useGetIdentity,
  UserIdentity,
  useTranslate,
} from 'react-admin'
import { ResourceName } from '../core/ResourceName'
import { FindUserByTicket } from './common/FindUserByTicket'
import { EventList } from './events/EventList'
import { LogList } from './logs/LogList'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    header: {
      textAlign: 'center',
      letterSpacing: theme.spacing(0.2),
    },
  }),
)

const TicketFinder = () => (
  <Card style={{ width: '100%', marginBottom: '5px' }}>
    <FindUserByTicket />
  </Card>
)

const LastLogs = () => {
  const t = useTranslate()
  return (
    <Card style={{ width: '100%', marginBottom: '5px' }}>
      <CardHeader title={t('dashboard.cards.last-logs.title')} />
      <CardContent>
        <ResourceContextProvider value={ResourceName.LOGS}>
          <LogList
            basePath="/"
            title=" "
            compactMode
            pagination={false}
            perPage={10}
            actions={false}
            filters={undefined}
          />
        </ResourceContextProvider>
      </CardContent>
    </Card>
  )
}

const LastEvents = () => {
  const t = useTranslate()
  return (
    <Card style={{ width: '100%', marginBottom: '5px' }}>
      <CardHeader title={t('dashboard.cards.last-events.title')} />
      <CardContent>
        <ResourceContextProvider value={ResourceName.EVENTS}>
          <EventList
            sort={{ field: 'status', order: 'DESC' }}
            compactMode
            basePath="/"
            title=" "
          />
        </ResourceContextProvider>
      </CardContent>
    </Card>
  )
}

export const Dashboard = () => {
  const { identity } = useGetIdentity()
  const classes = useStyles()
  const t = useTranslate()

  const isItemShown = (item: string, userId?: UserIdentity) => {
    const show = localStorage.getItem(`${userId?.id}.${item}`)
    return show !== '0'
  }

  return (
    <Grid container spacing={1}>
      <Grid item xs={12}>
        <Card>
          <CardHeader
            className={classes.header}
            title={t('dashboard.welcome')}
            titleTypographyProps={{ variant: 'h4' }}
          />
        </Card>
      </Grid>
      <Grid item sm={6} xs={12}>
        {isItemShown('ticketfinder', identity) && <TicketFinder />}
        {isItemShown('lastevents', identity) && <LastEvents />}
      </Grid>
      <Grid
        item
        sm={6}
        xs={12}
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'flex-end',
          width: '10%',
        }}
      >
        {isItemShown('dashlogs', identity) && <LastLogs />}
      </Grid>
    </Grid>
  )
}
