import {
  List, ListProps, FilterProps, Filter, TextInput, useListContext, Datagrid,
  TextField, DateField, FunctionField, useTranslate, Record, Pagination, Link
} from "react-admin"
import React from "react"
import { TableBody, TableCell, TableHead, TableRow, IconButton } from "@material-ui/core"
import { Sort, Event } from '@material-ui/icons'
import { ResourceName } from "../../core/ResourceName"
import EventType from "../../core/events/event"

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)


const EventsBody = () => {
  const translate = useTranslate()
  const { ids, data } = useListContext()
  return (
    <TableBody>
      {ids.map((id) => (
        <TableRow key={id}>
          <TableCell>
            <FunctionField record={data[id] as EventType} render={(record: Record | undefined) =>
              `${record?.id}. ${record?.name}`} />
          </TableCell>
          <TableCell>
            <FunctionField resource="events" label={translate("resources.events.fields.status")}
              record={data[id] as EventType} render={(r: Record | undefined) =>
                (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
          </TableCell>
          <TableCell padding="none">
            <Link to={`/events/${id}/show`}>
              <IconButton>
                <Event />
              </IconButton>
            </Link>
          </TableCell>
          <TableCell padding="none">
            <Link to={`/stats-overall-view/${id}/show`}>
              <IconButton>
                <Sort style={{ transform: "scaleX(-1)" }} />
              </IconButton>
            </Link>
          </TableCell>
        </TableRow>
      ))}
    </TableBody>
  )
}

const EventsHead = () => {
  const translate = useTranslate()
  return (
    <TableHead>
      <TableRow>
        <TableCell>
          {translate('resources.events.fields.name')}
        </TableCell>
        <TableCell>
          {translate('resources.events.fields.status')}
        </TableCell>
        <TableCell />
        <TableCell />
      </TableRow>
    </TableHead>
  )
}

const EventGrid = ({ compactMode }: { compactMode?: boolean }) => {
  const { loading } = useListContext()
  const translate = useTranslate()
  return (
    <>
      {compactMode ? (
        <Datagrid body={EventsBody} header={EventsHead}>
          <div />
        </Datagrid>
      ) : (
        <Datagrid size="small" loaded={!loading} rowClick={(id) => `${ResourceName.EVENTS}/${id}/show`}>
          <TextField source="id" label="ID" />
          <TextField source="name" />
          <DateField source="dateStart" showTime />
          <DateField source="dateEnd" showTime />
          <FunctionField resource="events" label={translate("resources.events.fields.status")}
            render={(r: Record | undefined) =>
              (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
        </Datagrid>
      )}
    </>
  )
}

interface EventListProps extends ListProps {
  compactMode?: boolean
}

export const EventList = ({
  compactMode = false,
  ...props
}: EventListProps) => compactMode ?
    <List {...props} actions={false} filters={undefined} perPage={10}
      pagination={false}>
      <EventGrid compactMode />
    </List> :
    <List {...props} actions={false} filters={<ListFilters />} perPage={20}
      pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}>
      <EventGrid />
    </List>